import Vue from 'vue'
import axios from 'axios'
const instance = axios.create({
  baseURL: '/api',
  timeout: 1000,
  headers:{'Content-Type':'application/x-www-form-urlencoded'},
  transformRequest: function (data, headers) {
    return data;
  },
  transformResponse: function (data) {
    return data;
  },
});

export default instance