import Vue from 'vue'
import vuex from 'vuex'
Vue.use(vuex)
const store = new vuex.Store({
	state: {
		user_count: 0,
		user_info: {
			name: 'huangenai',
			age: 22,
			sex: '女'
		},
		user_cart: []
	},
	getters: { /* 通过this.$store.getters.getlist 调用 */
		get_cart_list: function(state) {
			return state.user_cart;
		},
		get_cart_list_goodsnums:function(state){
			return state.user_cart.length;
		}
	},
	mutations: { /* 通过this.$store.commit 调用 */
		add_goods: function(state, data) {
			let res = state.user_cart.findIndex((ele,index)=>{
				return ele.goodsId==data.goodsId;
			});
			if(res==-1){
				state.user_cart.push(data);
			}else{
				state.user_cart[res].nums++;
			}
		},
		add_goods_num: function(state, data) {
			state.user_cart[data.index].nums++;
		},
		reduce_goods_num: function(state, data) {
			state.user_cart[data.index].nums--;
		},
		remove_goods:function(state,data){
			state.user_cart.splice(data.index,1);
		}
	},
	actions: { /* 通过this.$store.dispatch('setCount', {count: 123}) 主要用于提交mutations */
		commit_addgoods_to_cartlist: function({commit}, data) {
			commit('add_goods', data);
		},
		commit_addgoodsnums_to_carlist: function({commit}, data) {
			commit('add_goods_num', data);
		},
		commit_reducegoodsnums_to_carlist: function({commit}, data) {
			commit('reduce_goods_num', data);
		},
		commit_removegoods_from_cartlist:function({commit},data){
			commit('remove_goods',data);
		}
	}
})
export default store
